'use strict'
import { JSEncrypt } from 'jsencrypt'
import config from '@/config/config.js'

/**
 * @description 加密字段
 * @param {String} text 需要加密的文本
 * @return {String}
 */
export function encrypted(text) {
  // 新建JSEncrypt对象
  let enCrypt = new JSEncrypt()
  // 设置公钥
  enCrypt.setPublicKey(config.webKey)
  // 加密数据
  return enCrypt.encrypt(text)
}

/**
 * @description 生成指定位数随机数
 * @param {Number} count 生成位数
 * @return {String}
 */
export function sixRandom(count = 6) {
  let random = Math.floor(Math.random() * (1 + '0'.repeat(count)))
  return random.toString()
}

/**
 * @description 倒叙字符串
 * @return {String}
 */
export function reverseString(string = '') {
  return string.toString().split('').sort().reverse().join('')
}

/**
 * @description 生产环境下使用webp格式的图片
 * @param {String} uri 图片地址
 * @return {Image}
 */
export function useWebpImg(uri) {
  uri = uri && uri.replace(/.*\/assets/, '')
  if (uri && window.isProduction) uri += '.webp'
  try {
    return require(`@/assets${uri}`)
  } catch (e) {
    console.error(e)
    return null
  }
}

/**
 * @description 获取uuid
 * @return {string} uuid
 */
export function getUUId() {
  return 'xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx'.replace(/[xy]/g, c => {
    return (c === 'x' ? (Math.random() * 16 | 0) : ('r&0x3' | '0x8')).toString(16)
  })
}

/**
 * @description 判断参数是否为对象
 *
 * @param  {*} target
 * @return {boolean}
 */
export function isObject(target) {
  return Object.prototype.toString.call(target) === '[object Object]'
}

/**
 * @description 判断参数是否为数组
 *
 * @param  {*} target
 * @return {boolean}
 */
export function isArray(target) {
  return Object.prototype.toString.call(target) === '[object Array]'
}

/**
 * @description 判断参数是否为方法
 *
 * @param  {*} target
 * @return {boolean}
 */
export function isFunction(target) {
  return Object.prototype.toString.call(target) === '[object Function]'
}

/**
 * @description 在目标元素后插入元素
 * @param newElement 插入元素
 * @param targetElement 目标元素
 */
export function insertAfter(newElement, targetElement) {
  let parent = targetElement.parentNode
  if (parent.lastChild === targetElement) {
    parent.appendChild(newElement)
  } else {
    parent.insertBefore(newElement, targetElement.nextSibling)
  }
}

export function utilInstall(Vue) {
  Object.defineProperties(Vue.prototype, {
    $useWebpImg: {
      get() {
        return useWebpImg
      },
      enumerable: false, // 不可枚举
      configurable: false // 不可重写
    },
    $encrypted: {
      get() {
        return encrypted
      },
      enumerable: false, // 不可枚举
      configurable: false // 不可重写
    }
  })
}

export default {
  getUUId,
  sixRandom,
  useWebpImg,
  reverseString
}
