<template>
  <header class="header border-bottom">
    <div class="header__left">
      <button v-if="showMenuButton" @click="()=>toggleSideBar(true)"
              class="header__button header__button--menu"></button>
      <slot v-else name="left">
        <button @click="callback" class="header__button header__button--back">
          返回
        </button>
      </slot>
    </div>
    <h1 class="header__title">
      <img v-show="showLogo" class="header__logo" :src="$useWebpImg('@/assets/img/header/logo.png')" alt="K8">
      <span v-show="!showLogo">{{title}}</span>
    </h1>
    <div class="header__right">
      <slot name="right">
        <button @click="signOut" class="header__button header__button--service"></button>
      </slot>
    </div>
  </header>
</template>

<script>
  import { mapState, mapMutations } from 'vuex'

  export default {
    name: 'Header',
    data() {
      return {
        formPath: null
      }
    },
    computed: {
      ...mapState(['pageTitle']),
      title() {
        return this.pageTitle || this.$route.meta.title
      },
      showLogo() {
        return this.$route.meta.showLogo
      },
      showMenuButton() {
        return this.$route.meta.showMenuButton
      }
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.formPath = from.path
      })
    },
    methods: {
      ...mapMutations([
        'logout',
        'toggleSideBar'
      ]),
      callback() {
        let toHome = !this.formPath
        if (toHome) {
          this.$router.replace('/')
        } else {
          this.$router.back()
        }
      },
      signOut() {
        this.$api.logout().then(({ head, body }) => {
          if (head['errCode'] === '0000') {
            this.$router.push('/')
            this.logout(body.info)
          }
        })
      }
    }
  }
</script>

