<template>
  <transition name="fade-common" @afterLeave="afterLeave">
    <div v-show="show" @click="show=false" id="_toast" class="toast">
      <i :class="[type]" class="toast__icon"></i>
      <p class="toast__text">{{message}}</p>
    </div>
  </transition>
</template>

<script>
  import { isFunction } from '@/utils/util.js'

  export default {
    name: 'toast',
    data() {
      return {
        show: false,
        timer: null,
        created: false,
        type: 'success',
        message: null,
        callback: null
      }
    },
    methods: {
      afterLeave() {
        clearTimeout(this.timer)
        isFunction(this.callback) && this.callback()
      }
    }
  }
</script>

<style lang="scss" scoped>
  .toast {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 998;
    width: rem(160);
    min-height: rem(100);
    margin-left: rem(-80);
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: rem(10);
    transform: translateY(-50%);

    &__icon {
      display: block;
      width: 100%;
      height: rem(70);
      vertical-align: top;
      background: no-repeat center;
      background-size: rem(44) auto;

      &.success {
        @include background-image('~@/assets/img/components/toast/success.png');
      }

      &.error {
        @include background-image('~@/assets/img/components/toast/error.png');
      }
    }

    &__text {
      padding: 0 rem(5) rem(5);
      font-size: rem(12);
      line-height: rem(15);
      color: $--color-white;
      text-align: center;
    }
  }
</style>
