<template>
  <div class="banner default-bg-small">
    <Swipe v-if="bannerList.length" :auto="5000" :showIndicators="false">
      <SwipeItem v-for="(item, index) in bannerList" :key="index"
                 @click.native="handelClick(item, index)"
                 class="default-bg-small">
        <img class="banner__img" v-lazy="item['imgurl']" src="" alt="">
      </SwipeItem>
    </Swipe>
  </div>
</template>

<script>
  import { getUUId } from '@/utils/util'
  import { Swipe, SwipeItem } from 'mint-ui'
  import { getWmsStatic } from '@/api/modules/wms'

  export default {
    name: 'Banner',
    components: {
      Swipe,
      SwipeItem
    },
    data() {
      return {
        bannerList: []
      }
    },
    created() {
      let url = '/_banner/banner.txt'
      let params = {
        params: {
          _: getUUId()
        }
      }
      getWmsStatic(url, params).then(({ data }) => {
        let bannerList = data['index_new']['slider_data']
        bannerList.forEach(item => {
          item['imgurl'] = `${window.dynamicConfig.wmsServer}/${item['imgurl']}`
          item['imgurl'] += window.isSupportWebp ? '_.webp' : ''
        })
        this.bannerList = bannerList
      })
    },
    methods: {
      handelClick(item, index) {
        console.log(item, index)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .banner {
    height: rem(170);

    &__img {
      width: 100%;
      height: 100%;
      vertical-align: top;
    }
  }
</style>
