import Vue from 'vue'
import store from '../store'
import VueRouter from 'vue-router'
import Home from '../views/home/home.vue'
import Header from '../components/Header.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    meta: {
      title: '首页',
      index: 1,
      showLogo: true,
      showMenuButton: true
    },
    components: {
      header: Header,
      default: Home
    }
  },
  {
    path: '/about',
    name: 'about',
    meta: {
      title: '关于',
      index: 2
    },
    components: {
      header: Header,
      default: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    }
  },
  {
    path: '/referralPartner',
    name: 'referralPartner',
    meta: {
      title: '超级合伙人',
      index: 3,
      keepAlive: false
    },
    components: {
      default: () => import('../views/referralPartner.vue')
    }
  },
  {
    path: '/test',
    name: 'test',
    meta: {
      title: '无效果页'
    },
    components: {
      default: () => import('../views/test.vue')
    }
  },
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '登陆',
      keepAlive: false
    },
    components: {
      default: () => import('@/views/sign/sign_in.vue')
    }
  },
  {
    path: '/register',
    name: 'register',
    meta: {
      title: '注册',
      keepAlive: false
    },
    components: {
      default: () => import('@/views/sign/sign_up.vue')
    }
  },
  {
    path: '/registerSuccess',
    name: 'registerSuccess',
    meta: {
      title: '注册成功',
      keepAlive: false
    },
    components: {
      default: () => import('@/views/sign/sign_up_success.vue')
    }
  },
  {
    path: '/dewFigure',
    name: 'dewFigure',
    meta: {
      title: '露珠图',
      index: 2
    },
    components: {
      header: Header,
      default: () => import('../views/web/dewFigure.vue')
    }
  },
  {
    path: '/404',
    name: '404',
    meta: {
      title: '404',
      keepAlive: false
    },
    components: {
      default: () => import('@/views/common/404.vue')
    }
  },
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

router.beforeEach((to, from, next) => {
  next()
})

export default router
